import { Metadata } from 'next';
import { Jost } from 'next/font/google';

import './globals.css';
import { query } from '@/lib/strapi';

// Configura la fuente Jost
const jost = Jost({
  subsets: ['latin'],
  weight: ['400', '500', '600', '700'],
  variable: '--font-jost',
  display: 'swap',
});

// Fetch para obtener datos de Strapi
const fetchGlobalData = async (): Promise<{
  siteName: string;
  siteDescription: string;
  defaultSeo: {
    metaTitle: string;
    metaDescription: string;
    shareImage?: { url: string }; // Opcional si `shareImage` está presente
  };
}> => {
  return query('global?populate=defaultSeo.shareImage').then((res) => {
    const { siteName, siteDescription, defaultSeo } = res.data;
    return {
      siteName: siteName || 'Default Site Name',
      siteDescription: siteDescription || 'Default Site Description',
      defaultSeo: {
        metaTitle: defaultSeo?.metaTitle || 'Default Meta Title',
        metaDescription:
          defaultSeo?.metaDescription || 'Default Meta Description',
        shareImage: defaultSeo?.shareImage?.data?.attributes
          ? { url: defaultSeo.shareImage.data.attributes.url }
          : undefined,
      },
    };
  });
};

// Función para generar metadata dinámico
export async function generateMetadata(): Promise<Metadata> {
  try {
    const {
      siteName: title,
      siteDescription: description,
      defaultSeo,
    } = await fetchGlobalData();

    return {
      title,
      description,
      openGraph: {
        title: defaultSeo?.metaTitle || title,
        description: defaultSeo?.metaDescription || description,
        images: defaultSeo?.shareImage
          ? [{ url: defaultSeo.shareImage.url }]
          : undefined,
      },
    };
  } catch (error) {
    console.error('Error fetching metadata:', error);
    return {
      title: 'Default Site Name',
      description: 'Default Site Description',
    };
  }
}

// Layout principal
export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body className={`${jost.variable} font-sans antialiased`}>
        {children}
      </body>
    </html>
  );
}
